import React from 'react';


class GetLastBetNumber extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      betNumber: 0,
      msgsender: "",
      res: undefined
    }
    this.getLastBetNumber = this.getLastBetNumber.bind(this)
  }
  render() {
    return (
      <div className='functionView'>
        <button onClick={this.getLastBetNumber} className='functionViewButton'>getLastBetNumber</button>
        <p>{this.state.res===undefined?null:this.state.res.toString()}</p>
      </div>
    );
  }
  getLastBetNumber() {
    console.log("getLastBetNumber call")
    this.props.multiBetContract.methods.getLastBetNumber().call().then(res => {console.log(res);this.setState({ res: res })})
  }
}
GetLastBetNumber.propTypes = {};

GetLastBetNumber.defaultProps = {};

export default GetLastBetNumber;
