import React from 'react';


class GetBetNumberToBetName extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      betNumber: 0,
      msgsender: "",
      res: undefined
    }
    this.getBetNumberToBetName = this.getBetNumberToBetName.bind(this)
  }
  render() {
    return (
      <div className='functionView'>
        <button onClick={this.getBetNumberToBetName} className='functionViewButton'>getBetNumberToBetName</button>
        <input min="0" placeholder="betNumber" type='number' className='intInput' onChange={e => this.setState({betNumber:e.target.value})}></input>
        <p>{this.state.res===undefined?null:this.state.res===""?"bet doesn't exist":this.state.res.toString()}</p>
      </div>
    );
  }
  getBetNumberToBetName() {
    console.log("getBetNumberToBetName call")
    this.props.multiBetContract.methods.getBetNumberToBetName(this.state.betNumber).call().then(res => {console.log(res);this.setState({ res: res })})
  }
}
GetBetNumberToBetName.propTypes = {};

GetBetNumberToBetName.defaultProps = {};

export default GetBetNumberToBetName;
