import React from 'react';


class BetOn extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      betNumber: 0,
      msgsender: "",
      res: undefined
    }
    this.betOn = this.betOn.bind(this)
  }
  render() {
    return (
      <div className='functionView'>
        <button onClick={this.betOn} className='functionSendButton'>betOn</button>
        <input min="0" placeholder="betNumber" type='number' className='intInput' onChange={e => this.setState({ betNumber: e.target.value })}></input>
        <input min="0" placeholder="option" type='number' className='intInput' onChange={e => this.setState({ option: e.target.value })}></input>
        <input min="0" placeholder="amount" type='number' className='intInput' onChange={e => this.setState({ amount: e.target.value })}></input>
      </div>
    );
  }
  betOn() {
    console.log("bet Try")
    this.props.multiBetContract.methods.betOn(this.state.betNumber, this.state.option, this.state.amount).send({ from: this.props.address })
      .once('receipt', (receipt) => {
        console.log("bet success")
      })
  }
}
BetOn.propTypes = {};

BetOn.defaultProps = {};

export default BetOn;
