import React from 'react';


class GetBetOptions extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      betNumber: 0,
      msgsender: "",
      res: undefined
    }
    this.getBetOptions = this.getBetOptions.bind(this)
  }
  render() {
    return (
      <div className='functionView'>
        <button onClick={this.getBetOptions} className='functionViewButton'>getBetOptions</button>
        <input min="0" placeholder="betNumber" type='number' className='intInput' onChange={e => this.setState({betNumber:e.target.value})}></input>
        <p>{this.state.res===undefined?null:this.state.res===""?"bet doesn't exist":this.state.res.toString()}</p>
      </div>
    );
  }
  getBetOptions() {
    console.log("getBetNumberToBetName call")
    this.props.multiBetContract.methods.getBetOptions(this.state.betNumber).call().then(res => {console.log(res);this.setState({ res: res })})
  }
}
GetBetOptions.propTypes = {};

GetBetOptions.defaultProps = {};

export default GetBetOptions;
