import React from 'react';


class SeeMyP2PBetsUserDetail extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      msgsender: "",
      betNumber:0
    }
    this.seeMyP2PBetsUserDetail = this.seeMyP2PBetsUserDetail.bind(this)
  }
  render() {
    return (
      <div className='functionView'>
        <button onClick={this.seeMyP2PBetsUserDetail} className='functionViewButton'>seeMyP2PBetsUserDetail</button>
        <input placeholder="address" type='text' className='addressInput' onChange={e => this.setState({msgsender:e.target.value})}></input>
        <input min="0" placeholder="betNumber" type='number' className='intInput' onChange={e => this.setState({betNumber:e.target.value})}></input>
        <p>{this.state.res === undefined ? null : this.state.res.toString()}</p>
      </div>
    );
  }
  seeMyP2PBetsUserDetail() {
    console.log("seeMyP2PBetsUserDetail call")
    this.props.multiBetContract.methods.seeMyP2PBetsUserDetail(this.state.msgsender,this.state.betNumber).call().then(res => { console.log(res); this.setState({ res: res }) })
  }
}

export default SeeMyP2PBetsUserDetail;
