import React from 'react';


class GetAmountInPoolFromUser extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      betNumber: 0,
      msgsender: "",
      res: undefined
    }
    this.getAmountInPoolFromUser = this.getAmountInPoolFromUser.bind(this)
  }
  render() {
    return (
      <div className='functionView'>
        <button onClick={this.getAmountInPoolFromUser} className='functionViewButton'>getAmountInPoolFromUser</button>
        <input min="0" placeholder="betNumber" type='number' className='intInput' onChange={e => this.setState({betNumber:e.target.value})}></input>
        <input min="0" placeholder="option" type='number' className='intInput' onChange={e => this.setState({option:e.target.value})}></input>
        <input placeholder="address" type='text' className='addressInput' onChange={e => this.setState({address:e.target.value})}></input>

        <p>{this.state.res===undefined?null:this.state.res===""?"bet doesn't exist":this.state.res.toString()}</p>
      </div>
    );
  }
  getAmountInPoolFromUser() {
    console.log("getBetNumberToBetName call")
    this.props.multiBetContract.methods.getAmountInPoolFromUser(this.state.betNumber,this.state.option,this.state.address).call().then(res => {console.log(res);this.setState({ res: res })})
  }
}
GetAmountInPoolFromUser.propTypes = {};

GetAmountInPoolFromUser.defaultProps = {};

export default GetAmountInPoolFromUser;
