import React from 'react';


class GetClosed extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      betNumber: 0,
      msgsender: "",
      res: undefined
    }
    this.getClosed = this.getClosed.bind(this)
  }
  render() {
    return (
      <div className='functionView'>
        <button onClick={this.getClosed} className='functionViewButton'>getClosed</button>
        <input min="0" placeholder="betNumber" type='number' className='intInput' onChange={e => this.setState({betNumber:e.target.value})}></input>
        <p>{this.state.res===undefined?null:this.state.res===""?"bet doesn't exist":this.state.res.toString()}</p>
      </div>
    );
  }
  getClosed() {
    console.log("getBetNumberToBetName call")
    this.props.multiBetContract.methods.getClosed(this.state.betNumber).call().then(res => {console.log(res);this.setState({ res: res })})
  }
}
GetClosed.propTypes = {};

GetClosed.defaultProps = {};

export default GetClosed;
