import React from 'react';


class GetCanceled extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      betNumber: 0,
      msgsender: "",
      res: undefined
    }
    this.getCanceled = this.getCanceled.bind(this)
  }
  render() {
    return (
      <div className='functionView'>
        <button onClick={this.getCanceled} className='functionViewButton'>getCanceled</button>
        <input min="0" placeholder="betNumber" type='number' className='intInput' onChange={e => this.setState({betNumber:e.target.value})}></input>
        <p>{this.state.res===undefined?null:this.state.res===""?"bet doesn't exist":this.state.res.toString()}</p>
      </div>
    );
  }
  getCanceled() {
    console.log("getBetNumberToBetName call")
    this.props.multiBetContract.methods.getCanceled(this.state.betNumber).call().then(res => {console.log(res);this.setState({ res: res })})
  }
}
GetCanceled.propTypes = {};

GetCanceled.defaultProps = {};

export default GetCanceled;
