import React from 'react';


class CreateNewBets extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      names: [],
      optionsNumbers: [],
      numberOfBetsToAdd:[],
      res: undefined
    }
    this.createNewBets = this.createNewBets.bind(this)
  }
  render() {
    return (
      <div className='functionView'>
        <button onClick={this.createNewBets} className='functionSendButton'>createNewBets</button>
        <input placeholder="names" type='text' className='intInput' onChange={e => this.setState({ names: e.target.value.split(",") })}></input>
        <input placeholder="optionsNumbers" type='text' className='intInput' onChange={e => this.setState({ optionsNumbers: e.target.value.split(",").map(Number) })}></input>
        <input min="0" placeholder="numberOfBetsToAdd" type='number' className='intInput' onChange={e => this.setState({ numberOfBetsToAdd: e.target.value })}></input>
      </div>
    );
  }
  createNewBets() {
    console.log("bet Try")
    console.log(this.state.names, this.state.optionsNumbers, this.state.numberOfBetsToAdd)
    this.props.multiBetContract.methods.createNewBets(this.state.names, this.state.optionsNumbers, this.state.numberOfBetsToAdd).send({ from: this.props.address })
      .once('receipt', (receipt) => {
        console.log("bet success")
      })
  }
}
CreateNewBets.propTypes = {};

CreateNewBets.defaultProps = {};

export default CreateNewBets;
