import React from 'react';


class GetBetNameToBetNumber extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      betNumber: 0,
      msgsender: "",
      res: undefined
    }
    this.getBetNameToBetNumber = this.getBetNameToBetNumber.bind(this)
  }
  render() {
    return (
      <div className='functionView'>
        <button onClick={this.getBetNameToBetNumber} className='functionViewButton'>getBetNameToBetNumber</button>
        <input type='text' placeholder="betName" className='addressInput' onChange={e => this.setState({betNumber:e.target.value})}></input>
        <p>{this.state.res===undefined?null:this.state.res===""?"bet doesn't exist":this.state.res.toString()}</p>
      </div>
    );
  }
  getBetNameToBetNumber() {
    console.log("getBetNameToBetNumber call")
    this.props.multiBetContract.methods.getBetNameToBetNumber(this.state.betNumber).call().then(res => {console.log(res);this.setState({ res: res })})
  }
}
GetBetNameToBetNumber.propTypes = {};

GetBetNameToBetNumber.defaultProps = {};

export default GetBetNameToBetNumber;
