import React from 'react';


class GetAmountInPoolEnd extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      betNumber: 0,
      msgsender: "",
      res: undefined
    }
    this.getAmountInPoolEnd = this.getAmountInPoolEnd.bind(this)
  }
  render() {
    return (
      <div className='functionView'>
        <button onClick={this.getAmountInPoolEnd} className='functionViewButton'>getAmountInPoolEnd</button>
        <input min="0" placeholder="betNumber" type='number' className='intInput' onChange={e => this.setState({betNumber:e.target.value})}></input>
        <input min="0" placeholder="option" type='number' className='intInput' onChange={e => this.setState({option:e.target.value})}></input>

        <p>{this.state.res===undefined?null:this.state.res===""?"bet doesn't exist":this.state.res.toString()}</p>
      </div>
    );
  }
  getAmountInPoolEnd() {
    console.log("getBetNumberToBetName call")
    this.props.multiBetContract.methods.getAmountInPoolEnd(this.state.betNumber,this.state.option).call().then(res => {console.log(res);this.setState({ res: res })})
  }
}
GetAmountInPoolEnd.propTypes = {};

GetAmountInPoolEnd.defaultProps = {};

export default GetAmountInPoolEnd;
