import React from 'react';


class GetWinner extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      betNumber: 0,
      msgsender: "",
      res: undefined
    }
    this.getWinner = this.getWinner.bind(this)
  }
  render() {
    return (
      <div className='functionView'>
        <button onClick={this.getWinner} className='functionViewButton'>getWinner</button>
        <input min="0" placeholder="betNumber" type='number' className='intInput' onChange={e => this.setState({betNumber:e.target.value})}></input>
        <p>{this.state.res===undefined?null:this.state.res===""?"bet doesn't exist":this.state.res.toString()}</p>
      </div>
    );
  }
  getWinner() {
    console.log("getBetNumberToBetName call")
    this.props.multiBetContract.methods.getWinner(this.state.betNumber).call().then(res => {console.log(res);this.setState({ res: res })})
  }
}
GetWinner.propTypes = {};

GetWinner.defaultProps = {};

export default GetWinner;
