import React from 'react';


class GetAmountInPool extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      betNumber: 0,
      msgsender: "",
      res: undefined
    }
    this.getAmountInPool = this.getAmountInPool.bind(this)
  }
  render() {
    return (
      <div className='functionView'>
        <button onClick={this.getAmountInPool} className='functionViewButton'>getAmountInPool</button>
        <input min="0" placeholder="betNumber" type='number' className='intInput' onChange={e => this.setState({betNumber:e.target.value})}></input>
        <input min="0" placeholder="option" type='number' className='intInput' onChange={e => this.setState({option:e.target.value})}></input>

        <p>{this.state.res===undefined?null:this.state.res===""?"bet doesn't exist":this.state.res.toString()}</p>
      </div>
    );
  }
  getAmountInPool() {
    console.log("getBetNumberToBetName call")
    this.props.multiBetContract.methods.getAmountInPool(this.state.betNumber,this.state.option).call().then(res => {console.log(res);this.setState({ res: res })})
  }
}
GetAmountInPool.propTypes = {};

GetAmountInPool.defaultProps = {};

export default GetAmountInPool;
