import React, { useState, useEffect } from 'react';

function Balances(props) {
    const [balanceCreator, setBalanceCreator] = useState(0)
    const [balanceEnder, setBalanceEnder] = useState(0)
    useEffect(() => {
        if (props.web3) {
            props.web3.eth.getBalance("0x6d3DCcF2C028766D26a5382Fce9d898e75E6D629")
                .then(balance => {
                    console.log(props.web3.utils.fromWei(balance, 'ether')); // Convert balance from wei to ether
                    setBalanceCreator(props.web3.utils.fromWei(balance, 'ether'))
                })
                .catch(err => {
                    console.error(err);
                });
            props.web3.eth.getBalance("0x1deecf77dD59A35c9f68cf507d79DDbd8524fa65")
                .then(balance => {
                    console.log(props.web3.utils.fromWei(balance, 'ether')); // Convert balance from wei to ether
                    setBalanceEnder(props.web3.utils.fromWei(balance, 'ether'))
                })
                .catch(err => {
                    console.error(err);
                });
        }
    }, [props.web3])
    return (
            <div id='balance'>
                <a className='linkAddress' href='https://testnet.bscscan.com/address/0x6d3DCcF2C028766D26a5382Fce9d898e75E6D629'>Creator : {balanceCreator + " BNB"}</a>
                <a className='linkAddress' href='https://testnet.bscscan.com/address/0x1deecf77dD59A35c9f68cf507d79DDbd8524fa65'>Ender : {balanceEnder + " BNB"}</a>
            </div>
    );
}

export default Balances;