import React from 'react';


class GetDead extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      betNumber: 0,
      msgsender: "",
      res: undefined
    }
    this.getDead = this.getDead.bind(this)
  }
  render() {
    return (
      <div className='functionView'>
        <button onClick={this.getDead} className='functionViewButton'>getDead</button>
        <input min="0" placeholder="betNumber" type='number' className='intInput' onChange={e => this.setState({betNumber:e.target.value})}></input>
        <p>{this.state.res===undefined?null:this.state.res===""?"bet doesn't exist":this.state.res.toString()}</p>
      </div>
    );
  }
  getDead() {
    console.log("getBetNumberToBetName call")
    this.props.multiBetContract.methods.getDead(this.state.betNumber).call().then(res => {console.log(res);this.setState({ res: res })})
  }
}
GetDead.propTypes = {};

GetDead.defaultProps = {};

export default GetDead;
