import React from 'react';


class SeeMyP2PBetsUser extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      msgsender: "",
    }
    this.seeMyP2PBetsUser = this.seeMyP2PBetsUser.bind(this)
  }
  render() {
    return (
      <div className='functionView'>
        <button onClick={this.seeMyP2PBetsUser} className='functionViewButton'>seeMyP2PBetsUser</button>
        <input placeholder="address" type='text' className='addressInput' onChange={e => this.setState({msgsender:e.target.value})}></input>
        <p>{this.state.res === undefined ? null : this.state.res.toString()}</p>
      </div>
    );
  }
  seeMyP2PBetsUser() {
    console.log("seeMyP2PBetsUser call")
    this.props.multiBetContract.methods.seeMyP2PBetsUser(this.state.msgsender).call().then(res => { console.log(res); this.setState({ res: res }) })
  }
}
SeeMyP2PBetsUser.propTypes = {};

SeeMyP2PBetsUser.defaultProps = {};

export default SeeMyP2PBetsUser;
