import React, { Component } from 'react';
import './App.css';
import Web3 from "web3";
import { DECENTRABET_ABI, DECENTRABET_ADDRESS, MBT_ABI, MBT_ADDRESS, MULTIBET_ABI, MULTIBET_ADDRESS, USDT_ABI, USDT_ADDRESS } from "./config";
import GetLastBetNumber from './components/getLastBetNumber/getLastBetNumber';
import GetBetNumberToBetName from './components/getBetNumberToBetName/getBetNumberToBetName';
import GetBetNameToBetNumber from './components/getBetNameToBetNumber/getBetNameToBetNumber';
import GetBetOptions from './components/getBetOptions/getBetOptions';
import GetClosed from './components/getClosed/getClosed';
import GetDead from './components/getDead/getDead';
import GetCanceled from './components/getCanceled/getCanceled';
import GetAmountInPool from './components/getAmountInPool/getAmountInPool';
import GetAmountInPoolFromUser from './components/getAmountInPoolFromUser/getAmountInPoolFromUser';
import GetPercentageOnOptionFromUser from './components/getPercentageOnOptionFromUser/getPercentageOnOptionFromUser';
import GetWinner from './components/getWinner/getWinner';
import BetOn from './components/betOn/betOn';
import SeeMyP2PBetsUser from './components/seeMyP2PBetsUser/seeMyP2PBetsUser';
import SeeMyP2PBetsUserDetail from './components/seeMyP2PBetsUserDetail/seeMyP2PBetsUserDetail';
import CreateNewBets from './components/createNewBets/createNewBets';
import Balances from './components/balances/balances';
import GetAmountInPoolEnd from './components/getAmountInPoolEnd/getAmountInPoolEnd';
class App extends Component {
  constructor(props) {
    super(props)
    this.state = {
      "address": undefined,
      "web3": undefined
    }
  }
  connectWalletHandler = () => {
    console.log("connecting...")
    if (window.ethereum && window.ethereum.isMetaMask) {
      console.log("MetaMask Here!");

      window.ethereum
        .request({ method: "eth_requestAccounts" })
        .then((result) => {
          console.log(result[0])
          this.state.web3.setProvider(Web3.givenProvider)
          this.setState({ "address": result[0] });
          localStorage.setItem("walletType", "Metamask")
          console.log(this.state.address)
        })
        .catch((error) => {
          console.log(error)
        });
    } else {
      console.log("Need to install MetaMask");
      this.setState({
        errorMessage: "Please install MetaMask browser extension to interact",
      });
    }
  };
  componentDidMount() {
    const web3 = new Web3("https://data-seed-prebsc-1-s1.binance.org:8545");
    this.setState({ web3 })
    if (web3.givenProvider) {
      web3.setProvider(Web3.givenProvider)
    }
    const multiBetContract = new web3.eth.Contract(MULTIBET_ABI, MULTIBET_ADDRESS);
    this.setState({ multiBetContract });
  }
  render() {
    return (
      <div className="App">
        <div id='header'><a id='contractLink' href={'https://testnet.bscscan.com/address/' + MULTIBET_ADDRESS}>{MULTIBET_ADDRESS}</a>{this.state.address === undefined ? <button onClick={this.connectWalletHandler}>Connect</button> : <p>{this.state.address}</p>}</div>
        <Balances web3={this.state.web3}></Balances>
        <GetLastBetNumber multiBetContract={this.state.multiBetContract}></GetLastBetNumber>
        <GetBetNumberToBetName multiBetContract={this.state.multiBetContract}></GetBetNumberToBetName>
        <GetBetNameToBetNumber multiBetContract={this.state.multiBetContract}></GetBetNameToBetNumber>
        <GetBetOptions multiBetContract={this.state.multiBetContract}></GetBetOptions>
        <GetClosed multiBetContract={this.state.multiBetContract}></GetClosed>
        <GetDead multiBetContract={this.state.multiBetContract}></GetDead>
        <GetCanceled multiBetContract={this.state.multiBetContract}></GetCanceled>
        <GetAmountInPool multiBetContract={this.state.multiBetContract}></GetAmountInPool>
        <GetAmountInPoolEnd multiBetContract={this.state.multiBetContract}></GetAmountInPoolEnd>
        <GetAmountInPoolFromUser multiBetContract={this.state.multiBetContract}></GetAmountInPoolFromUser>
        <GetPercentageOnOptionFromUser multiBetContract={this.state.multiBetContract}></GetPercentageOnOptionFromUser>
        <GetWinner multiBetContract={this.state.multiBetContract}></GetWinner>
        <SeeMyP2PBetsUser multiBetContract={this.state.multiBetContract}></SeeMyP2PBetsUser>
        <SeeMyP2PBetsUserDetail multiBetContract={this.state.multiBetContract}></SeeMyP2PBetsUserDetail>
        <BetOn multiBetContract={this.state.multiBetContract} address={this.state.address}></BetOn>
        <CreateNewBets multiBetContract={this.state.multiBetContract} address={this.state.address}></CreateNewBets>
      </div>
    );
  }
}

export default App;
